<template>
  <div id="OperationInfo">
    <div class="content">
      <ul>
        <li><span>{{data.operationName}}</span></li>
        <li><span>{{data.operationLevel}}</span></li>
        <li>NNIS<span>{{data.nnis}}</span></li>
        <li><span>{{data.zeqi===1?'':''}}</span></li>
        <li>ASA<span>{{data.mazuiLevel}}</span></li>
        <li><span>{{data.shoushuzhenduan}}</span></li>
        <li><span>{{data.mazuifangshi}}</span></li>
        <li><span>{{data.mazuishi}}</span></li>
        <li><span>{{data.diuizhushou}}</span></li>
        <li><span>{{data.dierzhushou}}</span></li>
      </ul>
    </div>
    <div class="content">
      <ul>
        <li><span>{{data.shoushuzhe}}</span></li>
        <li>ID<span>{{data.operationID}}</span></li>
        <li><span>{{data.operationTime}}</span></li>
      </ul>
    </div>
    <div class="detail">
      <div class="title"></div>
      <ul style="padding: 17px 15px;background-color: #ffffff;">
        <li v-for="(item,index) in this.data.shoushuguocheng">{{item}}</li>
      </ul>
    </div>

  </div>
</template>

<script>
  export default{
    name: 'OperationInfo',
    components: {
    },
    data() {
      return {
        cates: [ '16/4/9', '16/4/8', '16/4/7', '17/4/6' ],
        data: {
          name: '',
          sex: 1,
          operationID: '6378135',
          department: '',
          zhuyuanhao: '23533',
          operationName: '',
          operationTime: '2017-11-22',
          operationLevel: 2,
          nnis: 4,
          zeqi: 1,
          mazuiLevel: 2,
          shoushuzhenduan: '',
          mazuifangshi: '',
          mazuishi: '',
          shoushuzhe: '',
          diuizhushou: '',
          dierzhushou: '',
          shoushuguocheng: [
            '1.',
            '2.',
            '3.',
            '4.'
          ]
        }
      };
    },
    methods: {
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" >
  @import "../common/stylus/index.styl"
  #OperationInfo
    padding-top:15px
    padding-bottom: 15px
    background-color: #f5f5f5
  .content
    /*width: 100%*/
    height: auto
    background-color: #ffffff
    padding: 15px
    margin-bottom: 15px
  .content ul li
    margin-bottom: 20px
    color: #666666
    &:last-child
      margin-bottom: 0
  .content ul li span
    color: #333336

  .detail .title
    height: 50px
    line-height: 50px
    padding-left:15px
    margin-bottom: 1px
    font-weight: bold
    color: #333336
    background-color: #ffffff
  .detail ul li
    display: flex
    flex-direction: row
    flex-wrap: wrap
    list-style: none
    text-indent: 18px
    margin-bottom: 10px
    color: #333336
    &:last-child
      margin-bottom: 0


</style>



