<template>
    <div class="HospitalRecord">
      <!---->
      <div class="cate">
        <ul v-bind="{style:'width:'+ulWidth+'px'}">
          <li v-for="(item,index) in this.cates" :class="{selected: index===currentIndex}"  @click="ItemClick(index)">{{item}}</li>
        </ul>
      </div>
      <!---->
      <div class="patientInfo">
        <div class="patient-date">
          <span>{{data.ruyuan}}</span>
        </div>
        <div class="patient-item">
          <p>
            <span></span>{{data.name}}
          </p>
          <p>
            <span></span>{{data.zhusu}}
          </p>
          <p>
            <span></span>
            {{data.xianbingshi}}
          </p>
          <p>
            <span></span>{{data.zhusu}}
          </p>
          <p>
            <span></span>{{data.zhusu}}
          </p>
          <p>
            <span></span>{{data.zhusu}}
          </p>
          <p style="margin-bottom: 0">
            <span></span>{{data.zhusu}}
          </p>
        </div>
        <div style="margin: 15px 0">
          <div class="PhysicalCheck"></div>
          <div class="PhysicalCheckDetail">
            <p style="margin-bottom: 8px;">
              <span></span><span>37.5C</span><span></span><span>80/</span>
              <span></span><span>18/</span>
            </p>
            <p>
              <span></span>80/120mmHg,
              <span></span>60kg
            </p>
            <p style="margin-top: 17px">
              
            </p>
          </div>
        </div>
        <div style="margin: 15px 0">
          <div class="PhysicalCheck"></div>
          <div class="CheckInfo">
            <img src="http://p3.so.qhimgs1.com/bdr/_240_/t018f700e3623ab9833.jpg" style="width: 100%">
          </div>
        </div>
        <div style="margin: 15px 0">
          <div class="PhysicalCheck"></div>
          <table border="1">
            <tr style="height: 70px">
              <td></td>
              <div class="EyesCheck">
                <td colspan="2"></td>
                <td></td>
                <td></td>
              </div>

            </tr>
            <tr style="height: 35px">
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            <tr style="height: 35px">
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            <tr style="height: 35px">
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
          </table>

        </div>

        <div class="OtherCheck">
          <p></p>
          <p></p>
          <div class="Submit_button">
            <mt-button type="default" size="large"></mt-button>
            <mt-button style="margin-left: 10px" type="default" size="large"></mt-button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  export default {
      name: 'HospitalRecord',
      components: {
      },
       data() {
            return {
              currentIndex: 0,
              cates: ['16/4/9', '16/4/8', '16/4/7', '17/4/6'], // 
              ulWidth: 0,
              data: {
                name: '',
                sex: '1',
                nation: '',
                age: '20',
                marriage: '1',
                department: '',
                bed: '5',
                inpatientNumber: '234235',
                zhiye: '',
                chushengdi: '',
                ruyuan: '2017-10-30 11:30',
                chuyuan: '2017-11-23 12:00',
                chenshuzhe: '',
                zhusu: '4',
                xianbingshi: ''

              }
            };
        },
      created() {
           // 
        this.getcates();
      },
      methods: {
          getcates() {
              // ul=*
            let w = 60;
            let count = this.cates.length;
            this.ulWidth = w * count;
          },
          ItemClick(index) {
            this.currentIndex = index;
          }
      }
    };
</script>
<style lang="stylus" rel="stylesheet/stylus" >
  @import "../common/stylus/index.styl"
  .HospitalRecord
    background-color: #f5f5f5
    .cate
      height: 50px
      background-color: #ffffff
      overflow-x: auto
      margin: 15px 0
      .selected
        color: #333
    .cate ul
      padding-left: 15px
      display: flex
      flex-direction: row
    .cate ul li
      list-style: none
      display: inline-block
      text-align: center
      line-height: 50px
      margin-right: 35px
      color: #999999
    .patientInfo
      height: auto
      .patient-date
        line-height: 50px
        background-color: #ffffff
        font-size: 15px
        padding-left: 15px
        color: #999999
       span
        color: #666666
    .patient-item
      height: auto
      background-color: #ffffff
      margin-top: 1px
      padding: 17px 15px
    .patient-item p
      color: #666666
      margin-bottom: 20px
      line-height: 30px
    .patient-item p span
      color: #999999
    .PhysicalCheck
      height: 50px
      font-size: 15px
      padding-left: 15px
      font-weight: bold
      box-sizing: border-box
      color: #333333
      line-height: 50px
      background-color: #ffffff
      margin-bottom: 1px
    .PhysicalCheckDetail
      padding: 17px 15px
      background-color: #ffffff
    .PhysicalCheckDetail p
      font-size: 15px
      color: #333333
      &>span
        color: #666666
    table
      width: 100%
      background-color: #ffffff
      &>tr
        height: 35px
        text-align: center
        display: flex
        &>td:first-child
          width: 100px
    .EyesCheck
      display: flex
      flex: 1
      flex-direction: row
      &>td
        flex: 1


    .OtherCheck
      padding: 20px 15px 0 15px
      background-color: #ffffff
      &>p
        margin-bottom: 20px
        /*margin-left: 15px*/
      &>.Submit_button
        display: flex
        flex-direction: row
        margin-top: 26px
        margin-bottom: 40px
        &>.mint-button
          flex: 1
          height: 35px
          width: 167px
          &.mint-button--default
            color: #333333
            background-color: #f5f5f5




</style>



