<template>
  <div id="div_follwup_case">
    <!---->
    <div class="time_list border-1px">
      <ul>
        <li v-for="(item,index) in this.timeList" :class="{selected: index === currentIndex}" class="time_list_item"  @click="itemClick(index)">{{item}}</li>
      </ul>
    </div>
    <!---->
    <div class="patientInfo border-1px">
      <div class="patient-date">
        <span class="patient_conent">2017-11-20 11:30</span>
      </div>
      <div class="patient-item">
        <p>
          <span class="patient_title"></span>
          <span class="patient_conent"></span>
        </p>
        <p>
          <span class="patient_title"></span>
          <span class="patient_conent">4</span>
        </p>
        <p style="margin-bottom: 0">
          <span class="patient_title"></span>
          <span class="patient_conent">
            4""""
            
            </span>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
      name: 'FollwupCase',
      methods: {
        itemClick: function (index) {
          this.currentIndex = index;
        }
      },
      data () {
          return {
              currentIndex: 0,
              timeList: ['16/4/9', '16/4/8', '16/4/7', '17/4/6'] // 
          };
      }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/mixin.styl";
  #div_follwup_case
    background-color: #f5f5f5
    .time_list
      position:relative
      background-color: #ffffff
      margin: 15px 0
      display: flex
      flex-direction: row
      padding-left:15px
      color: #999999
      border-top-1px(#e5e5e5)
      border-bottom-1px(#e5e5e5)
      .time_list_item
         list-style: none
         display: inline-block
         text-align: center
         line-height: 50px
         margin-right: 35px
         color: #999999
      .selected
        color:#333
    .patientInfo
      position:relative
      height: auto
      border-top-1px(#e5e5e5)
      .patient-date
        line-height: 50px
        background-color: #ffffff
        font-size: 15px
        padding-left: 15px
        color: #666666
    .patient-item
      height: auto
      background-color: #ffffff
      margin-top: 1px
      padding: 20px 15px
      .patient_title
         color:#666666
      .patient_conent
         color:#333333
    .patient-item p
      color: #999999
      margin-bottom: 20px
      line-height: 30px
</style>



